$primary-color: #00246B;
$secondary-color: #8AB6F9;

body{
    display: flex;
    justify-content: center;
    background-color: #CADCFC;
}


.image{
    height: 5vw;
    width: 5vw;
}

@mixin flex-center {
   display: flex;
   flex-direction: column;
   gap: 2px;
}

.title {
    color: $primary-color;
}

.container {
@include flex-center();
    h1 {
        font-size: 10vw;
        }
    .subtitle{
        color: $primary-color;
    }
}

.nav{
    display: flex;
    gap: 2vw;
}

@mixin link {
    text-decoration: none;
    color: $secondary-color;
}

.Link{
    @include link()
}

